/** Media Queries **/
.mensaje {
  margin-top: 5rem;
}
.mensaje .uptask {
  background: linear-gradient(to right, #2563EB 0%, #b1c7f8 100%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .mensaje .uptask {
    font-size: 10rem;
  }
}